:root {
  /* Light Theme Variables */
  --text-primary-light: #000;
  --bgPrimary-light: #fff;
  --bgSecondary-light: #F9F9F9;
  --navbar-bg-light: #001529;
  --navbar-text-light: #fff;
  --primary-light: #0071bd;
  --primary-hover-light: #005a9e;
  --light-blue-light: #e6f7ff; /* Renamed for clarity */
  --border-light: #d9d9d9;
  --pink-light: #eb2f96; /* Added pink for light theme */

  /* Dark Theme Variables */
  --text-primary-dark: #e0e0e0; /* Lighter text for dark backgrounds */
  --bgPrimary-dark: #121212;   /* Common dark theme background */
  --bgSecondary-dark: #1e1e1e; /* Slightly lighter dark secondary */
  --navbar-bg-dark: #1f1f1f;   /* Dark navbar background */
  --navbar-text-dark: #e0e0e0;
  --primary-dark: #1890ff;     /* Ant Design's primary blue, often good on dark */
  --primary-hover-dark: #40a9ff;
  --light-blue-dark: #11324e;  /* Darker shade for light blue equivalent */
  --border-dark: #333;         /* Darker border */
  --pink-dark: #c41d7f;   /* Added pink for dark theme */

  --transition-speed: 0.3s;

  /* Deprecated - will be replaced by theme-specific variables below */
  /* --text-primary: #000; */
  /* --bgPrimary: #fff; */
  /* --bgSecondary: #F9F9F9; */
  /* --primary: #0071bd; */
  /* --primary-hover: #005a9e; */
  /* --light-blue: #e6f7ff; */
  /* --border: #d9d9d9; */
}

body {
  /* Default to light theme variables */
  --text-primary: var(--text-primary-light);
  --bgPrimary: var(--bgPrimary-light);
  --bgSecondary: var(--bgSecondary-light);
  --navbar-bg: var(--navbar-bg-light);
  --navbar-text: var(--navbar-text-light);
  --primary: var(--primary-light);
  --primary-hover: var(--primary-hover-light);
  --light-blue: var(--light-blue-light);
  --border: var(--border-light);
  --pink: var(--pink-light); /* Added pink default */

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background-color: var(--bgPrimary);
  color: var(--text-primary);
  transition: background-color var(--transition-speed) ease, color var(--transition-speed) ease;
}

body[data-theme="dark"] {
  --text-primary: var(--text-primary-dark);
  --bgPrimary: var(--bgPrimary-dark);
  --bgSecondary: var(--bgSecondary-dark);
  --navbar-bg: var(--navbar-bg-dark);
  --navbar-text: var(--navbar-text-dark);
  --primary: var(--primary-dark);
  --primary-hover: var(--primary-hover-dark);
  --light-blue: var(--light-blue-dark);
  --border: var(--border-dark);
  --pink: var(--pink-dark); /* Added pink for dark */
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
}

a:hover {
  color: var(--pink) !important;
}

.app {
  display: flex;
  overflow-x: hidden;
  background-color: var(--bgPrimary);
  transition: background-color var(--transition-speed) ease;
}

.navbar {
  flex: 0;
  background-color: #001529;
  position: fixed;
  width: 250px;
  height: 100vh;
  z-index: 100;
  transition: transform var(--transition-speed) ease;
  left: 0;
  top: 0;
}

.nav-container {
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 250px;
  background-color: #001529;
  z-index: 99;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.1);
  transition: transform var(--transition-speed) ease;
}

.main {
  flex: 1;
  width: calc(100% - 250px);
  margin-left: 250px;
  min-height: 100vh;
  padding: 24px;
  transition: margin var(--transition-speed) ease, width var(--transition-speed) ease, background-color var(--transition-speed) ease;
  overflow-x: hidden;
  background-color: var(--bgPrimary); /* Ensure main area also uses themed background */
}

.routes {
  padding: 20px;
}

/* ðŸ”§ Alignment fix starts here */
.logo-container {
  background-color: var(--navbar-bg); /* Use themed variable */
  display: flex;
  align-items: center;
  justify-content: center; /* Changed from space-between to center for symmetry */
  padding: 0 24px;
  height: 70px;
  border-bottom: 1px solid var(--border); /* Use themed variable */
}

/* Add this new rule for the .logo div */
.logo {
