}

.logo a { /* This is the consolidated and correct rule for .logo a */
  color: var(--navbar-text); /* Use themed variable */
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
  white-space: nowrap; /* Prevent text from wrapping */
  /* Combined transitions from the two original .logo a rules */
