}

.logo img {
  height: 30px;
  width: 30px;
  object-fit: contain;
}

/* Styles for the Login Page - Consolidated */
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Horizontally center the form card */
  justify-content: center; /* Vertically center the form card */
  padding: 20px;
  width: 100%;
  min-height: 75vh; /* Adjust as needed to fill available space within .main */
  box-sizing: border-box; /* Ensure padding doesn't add to width/height causing overflow */
  /* display: none; */ /* Original commented out line, kept for reference if needed */
}

.login-container a { /* This was the first instance, kept it */
  background-color: var(--primary); /* Use themed variable */
  color: var(--navbar-text); /* Or a specific button text color variable */
  padding: 8px 16px;
  border-radius: 6px;
  border: none;
  font-weight: 500;
  cursor: pointer;
  transition: background-color var(--transition-speed) ease, transform var(--transition-speed) ease;
}

.login-container a:hover { /* This was the first instance, kept it */
  background-color: var(--primary-hover); /* Use themed variable */
  transform: translateY(-2px);
}
/* ðŸ”§ Alignment fix ends here */

/* Styles for the Login Page (continued from merged block) */
.login-container .ant-typography { /* Targets the <Title> component (e.g., "Login", "Sign Up") */
  color: var(--text-primary);
  margin-bottom: 24px !important; /* Ensure override if Ant Design has specific margins */
}

.login-form { /* Targets the <Form> component with className="login-form" */
  width: 100%;
  max-width: 400px; /* Set a max width for the form card */
  padding: 32px; /* More padding inside the card */
  background-color: var(--bgPrimary); /* Use theme variable for card background */
  border-radius: 8px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1); /* A bit more pronounced shadow */
  border: 1px solid var(--border); /* Use theme variable for border */
}

.login-form .ant-form-item { /* Targets Ant Design form items for consistent spacing */
  margin-bottom: 20px;
}

.login-form .login-form-button { /* Targets the main submit button */
  width: 100%; /* Make the button full-width */
  height: 40px; /* Standard button height */
  font-size: 16px;
}

/* Styling for the "Or register now!" / "login instead!" link button */
.login-form .ant-btn-link {
  padding: 0; /* Remove default padding if any to make it look more like a text link */
  color: var(--primary); /* Use theme's primary color for the link */
}

.login-form .ant-btn-link:hover {
  color: var(--primary-hover); /* Use theme's primary hover color */
}

.login-container .ant-alert { /* Styles for error/success messages */
  max-width: 400px; /* Match form width */
  width: 100%;
  margin-bottom: 20px;
}

/* The existing .login-container a rules might be for other specific links,
   or could be reviewed/removed if not used by the current Login.jsx structure.
   The duplicate rules below this comment block have been removed. */

.menu-control-container {
  display: none !important;
  position: absolute !important;
  right: 10px !important;
  top: 25px !important;
  font-size: 1.2rem !important;
  background-color: var(--bgSecondary) !important; /* Use themed variable */
  border: none !important;
  /* Consider text color for the icon if needed */
}

.loader {
  height: 81vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader::after {
  content: '';
  width: 50px;
  height: 50px;
  border: 4px solid var(--border); /* Use themed variable */
  border-top-color: var(--primary); /* Use themed variable */
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Consolidated media query */
@media screen and (max-width: 1300px) { /* Covers 1300px, 1170px, 1000px as they were identical */
  .main {
    margin-left: 250px;
  }
}

/* Removed redundant media queries for 1170px and 1000px that were identical to 1300px */

@media screen and (max-width: 800px) {
  .app {
    flex-direction: column;
    overflow: hidden;
  }

  .navbar {
    flex: 1;
    width: 100%;
    height: 60px;
  }

  .main {
    flex: 1;
    width: 100%;
    margin-top: 60px;
    margin-left: 0;
    padding: 0 15px;
  }

  .nav-container {
    height: 60px;
    width: 100%;
    background-color: rgb(0, 21, 41);
  }

  .menu-control-container {
    display: block !important;
  }

  .ant-menu {
    position: absolute;
    right: 0px;
  }

  .home-title {
    font-size: 1.4rem !important;
  }

  .show-more {
    font-size: 1.3rem !important;
  }
}




